import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import ListItemText from '@mui/material/ListItemText';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Slide from '@mui/material/Slide';
import HouseRoundedIcon from '@mui/icons-material/HouseRounded';
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <React.Fragment>
        <HouseRoundedIcon/>
      <Button className='mr-5 hover:text-gray-900 ' onClick={handleClickOpen}>
        Asosiy
      </Button>
      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <AppBar sx={{ position: 'relative' }}>
          <Toolbar>
            <IconButton
              edge="start"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
            >
              <CloseIcon />
            </IconButton>
            <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
              100K best Internet Magazini
            </Typography>
            <Button autoFocus color="inherit" onClick={handleClose}>
              Tushundim
            </Button>
          </Toolbar>
        </AppBar>
        <List>
          <ListItem button>
            <ListItemText primary="100Kbest.uz mahsulotlarni qancha vaqtda yetkazib beradi .?" 
            secondary="Javob : 3kunda mahsulot qo`lingizda bo`ladi" />
          </ListItem>
          <Divider />
          <ListItem button>
            <ListItemText
              primary="Dastavka hamma viloyatlarga mavjudmi .??"
              secondary="Javob: Ha , Dastavka barcha viloyatlarga mavjud"
            />
          </ListItem>
          <Divider />
          <ListItem button>
            <ListItemText
              primary="To`lovlar Oldindanmi .?"
              secondary="Javob: Yo`q , To`lov mahsulotni qo`lingizga olganda.?"
            />
          </ListItem>
          <Divider/>
          <ListItem button>
            <ListItemText
              primary="Oldindan to`lov qilish ham mumkinmi . ?"
              secondary="Javob: Ha "
            />
          </ListItem>
          <Divider/>
          <ListItem button>
            <ListItemText
              primary="To`lovlar qay tarzda to`lanadi . ?"
              secondary="Javob: Naqd pul shaklida, Humo , Uzcard , Mastercard , Visa , Sberbank va boshqa bir qancha tizimlar orqali"
            />
          </ListItem>
          <Divider/>
          <ListItem button>
            <ListItemText
              primary="Mahsulot yetkazib berilganda olish fikridan qaytishim mumkinmi , yoki mahsulotni sotib olishga majbur bo`lamanmi"
              secondary="Javob: Mahsulotni xohlagan paytda olish fikridan qaytishingiz mumkin"
            />
          </ListItem>
          <Divider/>
          <h4>Agar yana qandaydir tushunmagan savollaringiz bo`lsa , contact bo`limi orqali savolingizni adminga yozib qoldirishingiz mumkin .</h4>
        </List>
      </Dialog>
    </React.Fragment>
  );
}
