import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function KeepMountedModal() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
        <PeopleAltIcon/>
      <Button class="mr-5 text-blue-500 lsa hover:text-gray-900" onClick={handleOpen} >Contact</Button>
      <Modal
        keepMounted
        open={open}
        onClose={handleClose}
        aria-labelledby="keep-mounted-modal-title"
        aria-describedby="keep-mounted-modal-description"
      >
        <Box sx={style}>
          <Typography id="keep-mounted-modal-title" variant="h6" component="h2">
Admin bilan Aloqa          </Typography>
          <Typography id="keep-mounted-modal-description" sx={{ mt: 2 }}>
100Kbest Sayti orqali xohlagan mahsulotingizni buyurtma qilishingiz mumkin ,
Agarda qandaydir tushunmagan joyingiz , yoki qandaydir savollaringiz bo`lsa
 adminga pastdagi link orqali telegramdan savollaringizni berishingiz
  mumkin .
   Admin ish vaqti 12 : 00 dan 24 : 00 gacha .

  <a href='https://t.me/Jorayev_Nurmuhammad'>Adminga yozish</a>
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}