import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import InfoRoundedIcon from '@mui/icons-material/InfoRounded';
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function BasicModal() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <InfoRoundedIcon/>
      <Button onClick={handleOpen} className='mr-5 hover:text-blue-900 lsa'>Biz haqimizda</Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            100KBest Haqida qisqacha
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
100Kbest Internet magazini O`zbekistondagi eng arzon va sifatli mahsulotlari
mavjud Internet magazinlar qatoriga kiradi . Bizning Magzinimizdan o`zingizga kerakli
barcha mahsulotlarni topishingiz mumkin . Biz saytimizga faqat sifatli va kerakli mahsulotlarni qo`yamiz 
, bu esa sizga o`zingizga kerakli mahsulotlarni topishingizga yordam beradi . Xohlagan mahsulotingizni 
tanlang va buyurtma qiling , biz esa uni sizga yetkazib beramiz , to`lovni
mahsulotni qo`lingizga olganda qilasiz.
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}